.editor-container {
  min-height: 400px; /* エディタの最小高さ */
  border: 1px solid #e0e0e0;
  padding: 16px;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.editor-container h1 {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
  border-bottom: 2px solid #e0e0e0;
  padding-bottom: 5px;
}

.editor-container h2 {
  font-size: 1.75rem;
  font-weight: bold;
  margin-bottom: 8px;
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 4px;
}

.editor-container p {
  font-size: 1rem;
  line-height: 1.6;
}
